
.container {
    max-width: 500px;
    min-width: 200px;
    height: 400px;
    margin: auto;
    padding-top: 60px;
    /* //background:blue;
    //padding: 20px 20px;
    //border: 1px solid rgba(128, 128, 128, 0.267);
    //position: absolute;
    //left: 50%;
    //top: 50%;
    //transform: translate(-50%, -50%); */
    font-size: 0.9rem;
  }
  
  .container .title {
    display: flex;
  }
  
  .container .title > button {
    border: none;
    margin-left: 5px;
    height: 20px;
    align-self: center;
    background: none;
  }
  
  .container .title .pagination {
    margin-left: auto;
    align-self: center;
  }
  .container .title .pagination button {
    margin: 0 10px;
    border: none;
    background: blue;
  }
  
  .container .week {
    display: flex;
    color: gray;
  }
  
  .container .week .weekday {
    width: calc(100% / 7);
    text-align: center;
  }
  
  .date-bottom-dot {
    background-color: #6dd49d;
    width: 3px;
    height: 3px;
    margin: auto;
    display: block;
    margin-top: -7px;
    border-radius: 50%;
  }
  
  .container .week .sunday {
    /* //color: red; */
  }
  
  .container .week .saturday {
    /* //color: blue; */
  }
  
  .container .date {
    margin-top: 20px;
  }
  
  .container .date .weekday {
    width: calc(100% / 7);
    float: left;
    text-align: center;
    height: 50px;
    line-height: 50px;
  }
  
  .container .date .sunday {
    color: red;
  }
  
  .container .date .saturday {
    color: blue;
  }
