  .calendar-header {
    width: 100%;
    height: 50px;
    padding: 10px 15px;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: row;
    background: #714be1;
    z-index: 9999;
  }
  .calendar-header div {
    flex: 1;
  }
  .calendar-header .arrow-button {
    background: rgba(232, 232, 232, 1);
    color: black;
    padding: 5px 8px;
    font-size: 0.7rem;
    border-radius: 50%;
    border: 0;
  }
  .calendar-header .right {
    display: flex;
  }
  .calendar-header .right button {
    margin-left: auto;
  }
  
  .calendar-header .center p {
    margin: 0;
    text-align: center;
    line-height: 30px;
    color: white;
  }